* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

button {
  outline: none;
  border: none;
  cursor: pointer;
}

.App {
  text-align: center;
  width: 85%;
  max-width: 1920px;
  margin: 0 auto;
  box-shadow: 0 2px 32px 5px rgba(156, 156, 156, 0.9);
  min-height: 100vh;
  background-color: #2f741112;
  padding-bottom: 30px;
}

#search {
  margin: 18px 10% 25px -28%;
  width: 67%;
  min-height: 49px;
  border-radius: 16px;
  font-size: 20px;
  outline: none;
  border: 3px solid rgba(122, 250, 68, 0.5);
  padding: 8px 30px;
  transition: all 0.7s;
  color: grey;
}

#search:hover,
#search:focus {
  color: black;
  box-shadow: 0px 0px 4px 1.5px rgba(213, 222, 213);
}

.gallery {
  width: 100%;
  display: flex;
  justify-content: normal;
  align-items: center;
  flex-wrap: wrap;
  margin: 2% auto;
  padding: 0 2%;
}

.card {
  position: relative;
  margin: 20px 2.662%;
  width: 28%;
  min-height: 340px !important;
  text-align: center;
  border-radius: 5px;
  transition: all 0.6s;
  background-color: white;
}

.card img {
  width: 100%;
  height: 240px;
  transition: all 0.6s;
}

.remove-product {
  border-radius: 10%;
  background-color: yellow;
  position: absolute;
  top: 6px;
  right: -8px;
  width: 30px;
  height: 30px;
  font-weight: 800;
  transition: all 0.7s;
}

.to-top {
  border-radius: 2px;
  height: 32px;
  width: 72px;
  background-color: #ffff00;
  color: #000000;
  font-weight: 800;
  position: absolute;
  bottom: 91px;
  left: 9px;
  text-transform: uppercase;
  font-size: 14px;
  transition: all 0.7s;
}

.to-top:hover,
.remove-product:hover,
.card:hover {
  box-shadow: 1px 4px 30px -8px rgba(15, 18, 15, 0.57);
}

@media only screen and (max-width: 992px) {
  .card {
    position: relative;
    margin: 10px 4%;
    width: 42%;
    min-height: 230px;
    text-align: center;
    background-color: #ffffff;
    border-radius: 10px;
  }
}

@media only screen and (max-width: 576px) {
  .card {
    position: relative;
    margin: 10px auto;
    width: 80%;
    min-height: 230px;
  }
  #search {
    margin: 20px auto 40px auto;
    width: 81%;
    min-height: 30px;
    font-size: 16px;
  }
}
